// section ident mixin
//       .
//    - 60 , -
//  .      
//       ,   .
@mixin section-indent($pt: 60px, $pb: $pt) {
  padding-top: $pt;
  padding-bottom: $pb;
}

//flex mixin
//   .    display: flex,
// align-items: center, justify-content: center     
//    .
@mixin flex(
  $dsp: flex,
  $fw: false,
  $fd: false,
  $ai: center,
  $jc: center,
  $indent: false
) {
  @if $dsp {
    display: $dsp;
  }
  @if $fw {
    flex-wrap: $fw;
  }
  @if $fd {
    flex-direction: $fd;
  }
  @if $ai {
    align-items: $ai;
  }
  @if $jc {
    justify-content: $jc;
  }
  @if $indent {
    gap: $indent;
  }
}
