// position absolute mixin
//     .  
//       center.
@mixin position(
  $pos: absolute,
  $top: false,
  $right: false,
  $bot: false,
  $left: false
) {
  @if $pos {
    @if $pos == 'center' {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    } @else {
      position: $pos;
    }
  }

  @if $top {
    top: $top;
  }

  @if $right {
    right: $right;
  }

  @if $bot {
    bottom: $bot;
  }

  @if $left {
    left: $left;
  }
}
