// grid+flex-basis mixin
//         
//      (flex-basis).   
//  ,    ( ) 32 , 
//   ( )    .
//     . 
//           
//   __item     .
@mixin card-set($items: 1, $indentX: 32px, $indentY: $indentX) {
  display: flex;
  flex-wrap: wrap;
  gap: $indentX $indentY;

  & > [class*='__item'] {
    flex-basis: calc((100% - $indentY * ($items - 1)) / $items);
  }
}
