// fonts mixin
//     .    
//    ,     
// .
@mixin fonts(
  $fst: normal,
  $fw: false,
  $fs: 14px,
  $lh: false,
  $ls: 0.04em,
  $tt: false,
  $cl: false
) {
  @if $fst {
    font-style: $fst;
  }
  @if $fw {
    font-weight: $fw;
  }

  @if $fs {
    font-size: $fs;
  }

  @if $lh {
    line-height: decimal-round(calc($lh / $fs));
  }

  @if $ls {
    letter-spacing: $ls;
  }

  @if $tt {
    text-transform: $tt;
  }

  @if $cl {
    color: $cl;
  }
}
